html,body {
    width:100%;
    height:100%;
    margin:0;
    padding:0;
    font-size:100%;
    font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
    font-synthesis: none;
    -moz-font-feature-settings: 'kern';
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: #f5f5f5;
  }
#root{
    width:100%;
    height: 100%;
}
  .container{
      display: flex;
      justify-content: center;
      align-items: flex-start;
      width:100%;
      height: 100%;
      padding: 0px;
      overflow: auto;
      background: rgb(213, 224, 231);;
      
  }
  .loader{
    display: flex;
    justify-content: center;
    align-items: center;
    width:100%;
    height: 100%;
    border:1px solid #000;
    }
  .movies{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-flow: row wrap;
    width:100%;
    height: auto;
    border:1px solid #000;
  }
  .movie{
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    flex-flow: row nowrap;
    width:600px;
    height: auto;
    padding: 20px;
    margin:10px 10px 50px;
    border-radius: 5px;
    background: #fff;
  }

  .movie img {
      position: relative;
      top:-40px;
      width:100%;
      height: auto;
      max-width: 130px;;
  }
  .movie .movie_data{
      padding-left:20px;
  }
  .genres {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-flow: row nowrap;
    margin:0;
    padding:0;
  }
  .genres li{
      list-style: none;
  }